import React from "react";

import styled from "styled-components";

const Wrapper = styled.div`
  background-color: #ffffff;
  width: 300px;
  height: 400px;
  margin-top: 20px;
`;

const RecomendHeader = styled.header`
  height: 50px;
  text-align: center;
`;

const HeaderText = styled.p`
  padding-top: 28px;
  font-size: 20px;
`;

const Border = styled.hr`
  width: 20%;
  margin-left: 120px;
`;

const Column = styled.ul`
  list-style: none;
  margin-top: 60px;
  margin-left: 20px;
  margin-bottom: 0px;
`;

const Category = () => {
  const title = "カテゴリ別記事";
  const date = ["スマブラ全般 (0)", "逆vipの逆襲 (0)"];
  return (
    <Wrapper>
      <RecomendHeader>
        <HeaderText>{title}</HeaderText>
        <Border />
      </RecomendHeader>
      <main>
        <Column>
          {date.map((item, i) => {
            return <li>{item}</li>;
          })}
        </Column>
      </main>
    </Wrapper>
  );
};

export default Category;
